<template>
  <div id="app">
    <StyleEditor ref="styleEditor" :code="currentStyle"></StyleEditor>
    <ResumeEditor ref="resumeEditor" :markdown="currentMarkdown" :enableHtml="enableHtml"></ResumeEditor>
  </div>
</template>

<script>
  import StyleEditor from './components/StyleEditor'
  import ResumeEditor from './components/ResumeEditor'
  import './assets/reset.css'

  export default {
    name: 'app',
    components: {
      StyleEditor,
      ResumeEditor
    },
    data() {
      return {
        interval: 40,
        currentStyle: '',
        enableHtml: false,
        fullStyle: [
          `/*
* Inspired by https://jirengu-inc.github.io/animating-resume/dist/
* 大家好，我是小达，中宏立达俱乐部的online小助手
* 今天是3月8日，祝全体女神们节日哈皮~
* 小达想来想去也应该用前端技术做点什么，那么我们就娓娓道来吧~
*/


/*首先一些技术初始化的操作*/
* {
  -webkit-transition: all .3s;
  transition: all .3s;
}
/* 白色背景太单调了，我们来点背景 */
html {
  color: rgb(222,222,222); background: rgb(0,43,54); 
}
/* 文字离边框太近了 */
.styleEditor {
  padding: .5em;
  border: 1px solid;
  margin: .5em;
  overflow: auto;
  width: 45vw; height: 90vh;
}
/* 代码高亮 */
.token.selector{ color: rgb(133,153,0); }
.token.property{ color: rgb(187,137,0); }
.token.punctuation{ color: yellow; }
.token.function{ color: rgb(42,161,152); }

/* 加点 3D 效果呗 */
html{
  -webkit-perspective: 1000px;
          perspective: 1000px;
}
.styleEditor {
  position: fixed; left: 0; top: 0; 
  -webkit-transition: none; 
  transition: none;
  -webkit-transform: rotateY(10deg) translateZ(-100px) ;
          transform: rotateY(10deg) translateZ(-100px) ;
}

/* 接下来，小达准备作一幅画送给女神*/
/* 先召唤出画板*/
.resumeEditor{
  position: fixed; right: 0; top: 0;
  padding: .5em;  margin: .5em;
  width: 48vw; height: 90vh; 
  border: 1px solid;
  background: white;
  overflow: auto;
}
`,

`
/* 好了，我开始画了 */
.container {
    height: 425px;
    margin: 200px auto 0;
}
/* HEAD */
.head {
    background-color: #FFFF00;
    border: solid 4px #000000;
}
 
.head-up {
    border-bottom: 4px solid #FFFF00;
    height: 195px;
    width: 218px;
}
 
.head-down {
  border-radius: 50% 50% 50% 50%;
    height: 154px;
    left: 2px;
    top: 65px;
    width: 234px;
    position:absolute;
}
 
.head-middle {
    position: absolute;
    z-index: 2;
    top: 76px;
    left: 8px;
    height: 123px;
    width: 230px;
    border: medium none;
    border-radius: 50% 50% 50% 50%;
}
.ear {
  background-color: #FFFF00;
    border: 5px solid #000000;
}
.ear-left {
    left: -35px;
    top: -65px;
    width: 100px;
    height: 110px;
    border-radius: 5px 125% 0px 125%;    
}
 
.ear-left:before {
    background-color: #000000;
    content: "";
    height: 60px;
    left: -32px;
    position: absolute;
    top: -5px;
    width: 60px;
}
 
.ear-right {
    width: 140px;
    height: 80px;
    right: -77px;
    top: 70px;
    border-radius: 10px 125% 10px 125%; 
}
 
.ear-right:before {
    background-color: #000000;
    content: "";
    height: 60px;
    position: absolute;
    right: -5px;
    top: 62px;
    width: 65px;
}
 
.hold-face {
    position: absolute;
    z-index: 3;
    height: 222px;
    width: 238px;    
}
 
/* EYES */
.eye {
    height: 42px;
    top: 84px;
    width: 42px;
}

 
.iris {
    background-color: #FFFFFF;
    border-radius: 50% 50% 50% 50%;
    height: 16px;
    position: absolute;
    top: 7px;
    width: 16px;
}
 
.iris-small {
  width: 6px;
  height: 11px;
}
 

.eyebrow {
  width: 45px;
  height: 2px;
  background: #000000;
  position: absolute;
  top: 85px;
  z-index: 2;

  opacity: 0;
}
.eyebrow:before {
    background: #FFFF00;
    height: 20px;
    width: 50px;
    content: '';
    position: absolute;
    top: -20px;
}

/* NOSE */
.nose {
  position: absolute;
  top: 125px;
  left: 111px;
  width: 17px;
  height: 15px;
}
 
.nose-top {
    position: absolute;
    z-index: 1;
    top: 0;
    width: 100%;
    height: 2px;
    background-color: #000000;
    border-radius: 34% 34% 22% 22%;
}
 
.hold-nose-bottom {
  position: absolute;
    top: 1px;
    z-index: 1;
    height: 10px; 
  width: 17px;
    overflow: hidden;
}
 
.nose-bottom {
    height: 17px;
    width: 16px;
    background-color: #000000;
    border-radius: 34% 33% 33% 33%;
}
 
/* CHEEK */
.cheek {
    border: 2px solid #000000;
    height: 50px;
    top: 126px;
    width: 50px;
}
 
 
.light {
    position: absolute;
    top: 88px;
    width: 130px;
    height: 130px;
}

.hold-thunder {
    height: 35px;
    width: 30px; 
    z-index: 1; 
}
 
.thunder {
    position: absolute;
    background: #FFFFFF;
    height: 1px;
}

 
.thunder1 {
    width: 10px;
    left: 15px;
    top: 28px;
}
 
.thunder2{
    left: 8px;
    top: 25px;
    width: 10px;
 
    -webkit-transform: rotate(41deg);
       -moz-transform: rotate(41deg);
            transform: rotate(41deg);
}
 
.thunder3{
    left: 3px;
    top: 24px;
    width: 8px;
 
    -webkit-transform: rotate(-40deg);
       -moz-transform: rotate(-40deg);
            transform: rotate(-40deg);
}
 
/* MOUTH */
 
.hold-mouth {
    position: relative;
    top: 150px;
    left: 70px;
    height: 9px;
    width: 100px;
    overflow: hidden;
}
 
.mouth {
    position: absolute;
    border: solid 2px #000000;
    border-radius: 20px;
}
 
.mouth-left {
    height: 40px;
    left: 16px;
    top: -31px;
    width: 30px;
}
 
.mouth-right {
    height: 40px;
    right: 16px;
    top: -31px;
    width: 30px;
}
 
.mouth-center {
    height: 40px;
    right: 35px;
    top: -1px;
    width: 30px;
    background-color: #FFFF00;
    z-index: 2;
}
 
.mouth-open {
    opacity: 0;
    background: none repeat scroll 0 0 #000;
    border-radius: 46%;
    height: 50px;
    left: 105px;
    position: absolute;
    top: 153px;
    width: 30px;
    z-index: 10;
}
 
.mouth-open:before {
    border-width: 0 15px 40px 0;
    border-color: transparent #000 transparent transparent;;
    border-style: solid;
    content: "";
    height: 0;
    position: absolute;
    left: -8px;
    top: 4px;
    width: 0;
}
 
.mouth-open:after {
    border-color: #000 transparent transparent;
    border-style: solid;
    border-width: 40px 15px 0 0;
    content: "";
    height: 0;
    position: absolute;
    right: -8px;
    top: 4px;
    width: 0;
}
 
.tongue {
    background-color: #ed5460;
    border-radius: 50%;
    bottom: -2px;
    height: 75%;
    left: -4px;
    position: absolute;
    width: 136%;
  
}
 
.tongue:before {
     background: none repeat scroll 0 0 #ffff00;
    height: 103%;
    left: -1px;
    position: absolute;
}
 
.tongue:after {
    background: none repeat scroll 0 0 #ffff00;
    content: "";
    height: 105%;
    width: 19px;
}
 
/*呀，没有时间了，要去写代码了，到此为止吧，拜拜了~~~*/
/*今天玩得开心哦~*/
`],
        currentMarkdown: '',
        fullMarkdown: `测试测试测试测试测试测试`
      }
    },
    created() {
      this.makeResume()
    },

    methods: {
      makeResume: async function () {
        await this.progressivelyShowStyle(0)
        await this.progressivelyShowResume()
        await this.progressivelyShowStyle(1)
        await this.showHtml()
        await this.progressivelyShowStyle(2)
      },
      showHtml: function () {
        return new Promise((resolve, reject) => {
          this.enableHtml = true
          resolve()
        })
      },
      progressivelyShowStyle(n) {
        return new Promise((resolve, reject) => {
          let interval = this.interval
          let showStyle = (async function () {
            if(this.currentStyle.indexOf("好了，我开始画了") > -1){
              interval = 0;
            }
            let style = this.fullStyle[n]
            if (!style) { return }
            // 计算前 n 个 style 的字符总数
            let length = this.fullStyle.filter((_, index) => index <= n).map((item) => item.length).reduce((p, c) => p + c, 0)
            let prefixLength = length - style.length
            if (this.currentStyle.length < length) {
              let l = this.currentStyle.length - prefixLength
              let char;
              if(this.currentStyle.indexOf("好了，我开始画了") === -1){
                char = style.substring(l, l + 1) || ' '
                this.currentStyle += char
                if (style.substring(l - 1, l) === '\n' && this.$refs.styleEditor) {
                  this.$nextTick(() => {
                    this.$refs.styleEditor.goBottom()
                  })
                }
              }else{
                char = style.substring(l, l + 15) || ' '
                this.currentStyle += char
                this.$nextTick(() => {
                  this.$refs.styleEditor.goBottom()
                })
              }
              
              setTimeout(showStyle, interval)
            } else {
              resolve()
            }
          }).bind(this)
          showStyle()
        })
      },
      progressivelyShowResume() {
        return new Promise((resolve, reject) => {
          let length = this.fullMarkdown.length
          let interval = this.interval
          let showResume = () => {
            if(this.currentStyle.indexOf("好了，我开始画了") > -1){
              interval = 0;
            }
            if (this.currentMarkdown.length < length) {
              if(this.currentStyle.indexOf("好了，我开始画了") === -1){
                this.currentMarkdown = this.fullMarkdown.substring(0, this.currentMarkdown.length + 1)
                let lastChar = this.currentMarkdown[this.currentMarkdown.length - 1]
                let prevChar = this.currentMarkdown[this.currentMarkdown.length - 2]
                if (prevChar === '\n' && this.$refs.resumeEditor) {
                  this.$nextTick(() => this.$refs.resumeEditor.goBottom())
                }
              }else{
                this.currentMarkdown = this.fullMarkdown.substring(0, this.currentMarkdown.length + 15)
                let lastChar = this.currentMarkdown[this.currentMarkdown.length - 15]
                let prevChar = this.currentMarkdown[this.currentMarkdown.length - 16]
                if (prevChar === '\n' && this.$refs.resumeEditor) {
                  this.$nextTick(() => this.$refs.resumeEditor.goBottom())
                }
              }
              setTimeout(showResume, interval)
            } else {
              resolve()
            }
          }
          showResume()
        })
      }
    }
  }

</script>

<style scoped>
  #app {
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  }
  
  html {
    min-height: 100vh;
  }
  
  * {
    -webkit-transition: all .3s;
    transition: all .3s;
  }
</style>

<style>
  .styleEditor pre{
    -moz-user-select:none;
    user-select:none;
  }
</style>



